import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'index',
      component: function (resolve) {
        require(['../components/index.vue'], resolve);
      },
    },
    //{
    //  path: '/',
    //  name: 'son',
    //  component: function (resolve) {
    //    require(['../components/son.vue'], resolve);
    //  },
    //},
    {
      path: '/',
      name: 'mume',
      component: function (resolve) {
        require(['../components/mume.vue'], resolve);
      },
    },
    {
      path: '/addMsg',
      name: 'addMsg',
      component: function (resolve) {
        require(['../components/addMsg.vue'], resolve);
      },
    },
    {
      path: '/pay/:code',
      name: 'pay',
      component: function (resolve) {
        require(['../components/pay.vue'], resolve);
      },
    },
    {
      path: '/personalPre',
      name: 'personalPre',
      component: function (resolve) {
        require(['../components/personalPre.vue'], resolve);
      },
    },
    {
      path: '/h',
      name: 'Hello',
      component: function (resolve) {
        require(['../components/Hello.vue'], resolve);
      },
    },
    {
      path: '/HotelMsg',
      name: 'HotelMsg',
      component:  function (resolve) {
        require(['../components/HotelMsg.vue'], resolve);
      },
    },
    {
      path: '/Order/:orderCode/:timeflag',
      name: 'Order',
      component: function (resolve) {
        require(['../components/Order.vue'], resolve);
      },
    },
    {
      path: '/orderfill/',
      name: 'orderfill',
      component: function (resolve) {
        require(['../components/orderfill.vue'], resolve);
      },
    },
    {
      path: '/personal',
      name: 'personal',
      component: function (resolve) {
        require(['../components/personal.vue'], resolve);
      },
    },
    {
      path: '/personalList',
      name: 'personalList',
      component: function (resolve) {
        require(['../components/personalList.vue'], resolve);
      },
    },
    {
      path: '/login',
      name: 'login',
      component: function (resolve) {
        require(['../components/login.vue'], resolve);
      },
    },
    {
      path: '/m',
      name: 'mume',
      component: function (resolve) {
        require(['../components/mume.vue'], resolve);
      },
    },
    {
      path: '/map',
      name: 'map',
      component: function (resolve) {
        require(['../components/map.vue'], resolve);
      },
    },
    //{
    //  path: '/t',
    //  name: 'test',
    //  component: function (resolve) {
    //    require(['../components/test.vue'], resolve);
    //  },
    //},
  ]
})



