<template>
  <div>
    <div class="field-content">
      <!--<div :class="[{'float':!titleFlag},{'errtitle':isErrMsg}]" class="field-label">{{title}}</div>-->
      <div :class="[{'float':!titleFlag}]" class="field-label">{{title}}<i v-if="isErrMsg"
                                                                           style="color:red;padding-left: 0.3rem;">({{errMsg}})</i>
      </div>
      <div class="field-hint" v-show="placeholder" :class="[{'errtitle':isErrMsg}]">{{placeholders}}</div>
      <template v-if="type=='text'">
        <input type="text" class="field-input" ref="iptxt" @focus="onfocus"
               @keyup="onKeyUp" @blur="onblur" @input="oninput" :value="currentValue">
      </template>
      <template v-if="type=='password'">
        <input type="password" class="field-input" ref="iptxt" @focus="onfocus"
               @keyup="onKeyUp" @blur="onblur" @input="oninput" :value="currentValue">
      </template>
      <template v-if="type=='tel'||type=='code'">
        <input type="tel" class="field-input" ref="iptxt" @focus="onfocus"
               @keyup="onKeyUp" @blur="onblur" @input="oninput" :value="currentValue">
      </template>
      <span class="icon-Artboard8" @click="cance" v-if="currentValue"></span>
      <div v-if="showLine" class="line"></div>
      <div v-if="showLine" class="line1" :class="[{'w2':!lineFlag},{'err':isErrMsg}]"></div>
    </div>
    <!--<p class="err-msg" v-if="isErrMsg">{{errMsg}}</p>-->
  </div>
</template>
<script>
  export default {
    data () {
      return {
        titleFlag: true,
        lineFlag: true,
        placeholder: false,
        isErrMsg: false,
        currentValue: this.val,
        errMsg: '不能为空',
      }
    },
    created () {
      //this.$emit('contentChg',this.currentValue);
      if (this.val.length > 0) {
        this.titleFlag = false
      } else {
        this.titleFlag = true
      }
      if (this.type == 'text' && this.val) {
        this.$emit('validate');
        this.$emit('contentChg',this.currentValue);

      } else if (this.type == 'tel' && this.val.length == 1) {
        this.$emit('validate');
        this.$emit('contentChg',this.currentValue);
      } else if (this.type == 'code' && this.val) {
        this.$emit('validate');
        this.$emit('contentChg',this.currentValue);
      }
    },
    props: {
      value: {
        type: [String, Number],
        default: ''
      },
      title: {
        type: String,
        default: '',
      },
      val: {
        type: String,
        default: '',
      },
      placeholders: {
        type: String,
        default: '',
      },
      type: {
        type: String,
        default: 'text',
      },
      showCancel: {
        type: Boolean,
        default: true,
      },
      showLine: {
        type: Boolean,
        default: true,
      },
    },
    methods: {
      show(){
        this.flag = false;
      },
      oninput(){
        this.currentValue = this.$refs.iptxt.value
        this.$emit('input', this.currentValue);
      },

      onblur(){
        //this.$emit('contentChg',this.currentValue);
        if (this.currentValue.length == 0) {
          this.titleFlag = true;
          this.isErrMsg = true;
        } else {
          if (this.type == 'tel') {
            if (this.currentValue.length != 11) {
              this.flag = false;
              this.lineFlag = false;
              this.isErrMsg = true;
              this.errMsg = '手机号码填写错误'
            } else {
              this.flag = false;
              this.lineFlag = true;
              this.isErrMsg = false;
              this.$emit('validate');
              this.$emit('contentChg',this.currentValue);
            }
          } else if (this.type == 'code') {
            if (this.currentValue.length != 4) {
              this.flag = false;
              this.isErrMsg = true;
              this.lineFlag = false;
              this.errMsg = '验证码错误'
            } else {
              this.flag = false;
              this.isErrMsg = false;
              this.$emit('validate');
              this.$emit('contentChg',this.currentValue);
            }
          } else {
            this.flag = false;
            this.lineFlag = true;
            this.isErrMsg = false;
            this.$emit('validate');
            this.$emit('contentChg',this.currentValue);
          }
        }
      },

      onfocus(){
        this.titleFlag = false;
        this.lineFlag = false;
      },

      onKeyUp(){

      },

      cance(){
        var ipt = this.$refs.iptxt
        ipt.focus();
        this.currentValue = '';
        this.$emit('input', this.currentValue);
      }
    },
    computed: {},

    mounted(){

    },
    watch: {
      currentValue(val){
        //this.$emit('contentChg',val);
        if (val.length > 0) {
          this.placeholder = false;
          if (this.type == 'text') {
            this.lineFlag = false;
            this.isErrMsg = false;
            this.titleFlag = false;
            this.$emit('validate');
            this.$emit('contentChg',val);
          }
          if (this.type == 'tel' && val.length == 11) {
            this.lineFlag = false;
            this.isErrMsg = false;
            this.$emit('validate');
            this.$emit('contentChg',val);
          }
          if (this.type == 'code' && val.length == 4) {
            this.lineFlag = false;
            this.isErrMsg = false;
            this.$emit('validate');
            this.$emit('contentChg',val);
          }
        } else {
          this.placeholder = true;
        }
      },
      titleFlag(val){
        !val && (this.currentValue.length == 0) ? this.placeholder = true : this.placeholder = false;
      },
    }

  }
</script>
<style scoped>
  .field-content {
    /*height: 1.35rem;*/
    height: 1.44rem;
    width: 100%;
    position: relative;
    transition: all .5s;
    font-size: 0.35rem;
    color: #a1a1a1;
  }

  .icon-Artboard8 {
    z-index: 5;
    position: absolute;
    bottom: 0;
    padding: 0.2rem;
    right: 0;
  }

  .err-msg {
    margin-top: 0.12rem;
    font-size: 0.3rem;
    color: red;
  }

  .icon-Artboard8:active:before {
    opacity: .4;
  }

  .icon-Artboard8:before {
    font-size: 0.45rem;
  }

  .w2 {
    left: 0 !important;
    width: 100% !important;
    background-color: #3f3f3f !important;
  }

  .err {
    background-color: red !important;
  }

  .errtitle {
    color: red !important;
  }

  .line {
    width: 100%;
    transition: all .3s;
    position: absolute;
    left: 0;
    bottom: 0;
    height: 1px;
    z-index: 15;
    background-color: #DADFE3;
  }

  .line1 {
    transition: all 0.3s ease-in-out;
    width: 0;
    position: absolute;
    left: 50%;
    bottom: 0;
    height: 1px;
    z-index: 16;
    background-color: #ccc;
  }

  .field-label {
    transition: all .5s;
    position: absolute;
    /*bottom: 0.36rem;*/
    bottom: 0.408rem;
    left: 0;
    z-index: 9;
    font-size: 0.312rem;
  }

  .field-hint {
    transition: all .5s;
    position: absolute;
    bottom: 0.3rem;
    left: 0;
    z-index: 9;
  }

  .field-input {
    transition: all .2s;
    position: absolute;
    z-index: 10;
    left: 0;
    /*bottom: 0.02rem;*/
    bottom: 0.13rem;
    height: 0.624rem;
    line-height: 0.624rem;
    width: 80%;
    outline: none;
    border: none;
    font-size: 0.408rem;
    background-color: rgba(0, 0, 0, 0);
  }

  .float {
    /*transform: translateY(-0.222rem);*/
    transform: translateY(-0.4rem);
  }
</style>



