<template>
  <div>
    <div class="toast" :class="isShow?'change':''">000000</div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        isShow: false,
        placement: 'change',
      }
    },
    created () {
      this.currentValue = this.value || ''
    },
    props: {
      txt: {
        type: String,
        default: '提示信息',
      },

    },
    methods: {},
    computed: {},

    mounted(){
      var _t = this;
      setTimeout(function () {
        _t.isShow = true;
      }, 500)
      setTimeout(function () {
        _t.isShow = false;
      }, 3000)
    },

    beforeMount(){
      let parent;
      parent = document.querySelector('.toast');
      console.log(parent,'1');
      if (!parent) {
        parent = document.createElement('div');
        parent.classList.add('toast', this.placement);
        document.body.appendChild(parent);
      }
      console.log(parent,'2');

      parent.innerHTML='12333333333333333333333333'
//      console.log($el);
//      parent.appendChild(this.$el);
    },
    watch: {

    }

  }
</script>
<style scoped>
  .toast {
    position: fixed;
    bottom: 10%;
    left: 50%;
    transform: translateX(-50%);
    width: 40%;
    height: 0.8rem;
    line-height: 0.8rem;
    font-size: 0.288rem;
    color: #fff;
    text-align: center;
    background-color: rgba(0, 0, 0, .4);
    transition: all 1s;
    opacity: 0;
  }

  .change {
    transform: translate(-50%, -50px);
    opacity: 1;
  }

  .change1 {
    transform: translate(-50%, 0);
    opacity: 1;
  }


</style>



