// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import axios from 'axios';
import { TweenMax, TimelineMax } from 'gsap';
import {getnotice} from './common'
import router from './router'
import newComp from './newComponents/index.js'

var host = window.location.host

if (host == 'localhost:8080') {
  axios.defaults.baseURL = 'http://m.test02.fangcang.org/mice-mobile/';
} else {
  axios.defaults.baseURL = 'http://' + host + '/mice-mobile';
}

Object.keys(newComp).forEach((key) => {
  Vue.component(key, newComp[key]);
});

/*监听图片 图片正常显示*/
Vue.prototype.successLoadImg = function (event) {
  if (event.target.complete == true) {
    event.target.classList.remove("default-image");
    var imgParentNode = event.target.parentNode;
    console.log(imgParentNode);
    if (imgParentNode.classList.contains('show-img') == true) {
      imgParentNode.style.background = "#000";
    }
  }
};

/*图片加载错误*/
Vue.prototype.errorLoadImg = function (event) {
  event.target.classList.add("icon-Artboard21");
  event.target.src = ''
};

axios.defaults.timeout = 15000

axios.interceptors.request.use(config => {
  return config
}, error => {
  console.log('加载超时');
  return Promise.reject(error)
})

axios.interceptors.response.use(response => {
  if (response.data.isVerify && response.data.isVerify == 1) {
    window.location.href = response.data.wxAuthUrl;
  }

  if (response.data.result == 0 && response.data.failReason) {
    getnotice(response.data.failReason)
    //console.log(response.data.failReason);
  }
  return response
}, error => {
  console.log('加载失败');
  getnotice('请求超时,请重新加载')
  return Promise.reject(error)
})

Vue.prototype.$ajax = axios

export default axios
