<template>
  <div>
    <div class="inputStepAll">
      <div class="inputStep_l">
        <p >{{text}}</p>
      </div>
      <div class="inputStep">
        <span class="inputStep_l icon-a1" @click="reduce" :class="num<=limitNum[0]?'gary':''"></span>
        <div class="card_btn">
          <div @click="isIpmodal=true"><i class="item_num">{{num}}</i></div>
        </div>
        <span class="inputStep_r icon-Artboard5" @click="add" :class="num>=limitNum[1]?'gary':''"></span>
      </div>
    </div>

    <transition name="fade">
      <div class="ip-modal" v-if="isIpmodal">
        <div class="ip-modal-bg" @click="isIpmodal=false">
          <div class="ip-modal-card">
            <div class="ip-title">
              <p>{{modalTitle}}</p>
              <i v-if="showSubtitle&&limitNum[0]>1" class="ip-subtitle">该产品{{limitNum[0]}}间起订</i>
            </div>
            <div class="ip-body">
              <ul>
                <li
                  :class="[{'ip-flag':(index==idx)},{'ip-nochose':!(idx+1>=limitNum[0]&&idx+1<=limitNum[1])}]"
                  v-for="(n,idx) in optionNum" @click.stop="liClick(idx)">{{n}}
                  <i v-show="index==idx" class="icon-Artboard10"></i></li>
                <li v-if="showLast">
                  <slot ref="ref11"></slot>
                </li>
              </ul>
            </div>
            <div class="ip-foot" @click="isIpmodal=false">关闭</div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        num: this.limitNum[0],
        isIpmodal: false,
        index: this.limitNum[0]-1,
      }
    },
    props: {
      text: {
        type: String,
        default: '間',
      },

      showSubtitle: {
        type: Boolean,
        default: false,
      },
      showLast: {
        type: Boolean,
        default: false,
      },
      Section: {
        type: Array,
        default(){
          return [1, 5]
        }
      },
      modalFun: {
        type: Function,
        default(){

        }
      },
      nums: {
        type: Number,
        default: 1,
      },
      testNum: {
        type: Number,
        default: 1,
      },
      optionNum: {
        type: Number,
        default: 5,
      },

      modalTitle: {
        type: String,
        default: '标题',
      },
        limitNum: {
        type: Array,
        default(){
          return [1, 5]
        }
      },


    },
    methods: {
      add(){
        if (this.num < this.limitNum[1]){
          this.num++;
          this.index++
        }
        if(this.num==this.Section[1]){
          this.modalFun()
        }
      },
      reduce(){
        if (this.num > this.limitNum[0]) {
          this.index--
          this.num--;
        }
      },

      aa(){
        this.isIpmodal = true
      },

      liClick(idx){
        var _t = this;
        if (idx+1>=this.limitNum[0]&&idx+1<=this.limitNum[1]) {
          this.index = idx;
          this.num = idx + 1;
          setTimeout(function(){
            _t.isIpmodal = false;
          },300)

        }
      }
    },
    computed: {},
    mounted(){

    },
    watch: {
      nums(val){
        this.num = val;
      },
      num(val){
        this.$emit('input', val);
      },
      isIpmodal(val){
        if (val) {
          $('body').css('overflow-y', 'hidden');
        } else {
          $('body').css('overflow-y', 'scroll');
        }
      },
    }
  }
</script>
<style scoped>
  /*模态框*/
  .ip-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 999;
  }

  .ip-modal-bg {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 999;
    background-color: rgba(0, 0, 0, .4);
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .ip-subtitle {
    font-size: 0.288rem;
    color: #a1a1a1;
  }

  .ip-modal-card {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    border-radius: 0.1rem;
    background-color: #fff;
    font-size: 0.35rem;
    z-index: 1024;
  }

  .ip-foot {
    border-top: 1px solid #d8e1e1;
    padding-left: 0.3rem;
    min-height: 1rem;
    line-height: 1rem;
  }

  .ip-title {
    padding-left: 0.3rem;
  }

  .ip-title p {
    font-size: 0.4rem !important;
    padding: 0.3rem 0 0.1rem;
  }

  .ip-foot {
    text-align: center;
  }

  .ip-body {
    max-height: 7.4rem;
    /*padding: 0 0.3rem;*/
    min-height: 1rem;
    overflow-y: auto;
  }

  .ip-body ul {
  }

  .ip-body ul li {
    padding:0 0.6rem;
    height: 1.2rem;
    line-height: 1.2rem;
  }

  .ip-body ul li:active {
    background-color: #f1f1f1;
  }

  .ip-body ul li i {
    height: 0.3rem;
    float: right;
  }

  .ip-body ul li i:before {
    color: red;
  }

  .ip-flag {
    color: red;
  }

  .ip-nochose {
    color: #a1a1a1;
  }

  .ip-nochose:active{
    background-color: #fff !important;
  }

  .inputStepAll {
    width: 100%;
    height: 1.3rem;
    line-height: 1.3rem;
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.24rem;
  }

  .inputStep_l {
    height: 100%;
    line-height: 1.3rem;
    font-size: 0.36rem;
  }

  .inputStep_l .ip-flag{
    margin-top: 0.12rem;
  }

  .inputStep_l .ip-flag p{
    text-align: center;
    font-size: 0.35rem;
    line-height: 0.5rem !important;
  }


  .inputStep_l_notice{
    font-size: 0.288rem !important;
    color: #a1a1a1;
  }

  .inputStep {
    /*width: 60%;*/
    height: 1.3rem;
    line-height: 1.3rem;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
  }

  .inputStep span {
    margin-top: 0.15rem;
    display: inline-block;
    width: 1rem;
    height: 1rem;
    text-align: center;
    background-color: #fff;
    border: 1px solid #d8e1e1;
    border-radius: 50%;
  }

  .inputStep span:before{
    position: relative;
    top: -0.15rem;
  }

  .gary {
    opacity: .6;
    background-color: #f1f1f1;
  }

  .inputStep span:before {
    font-size: 0.42rem;
  }

  .inputStep span:active {
    background-color: #f7f7f7;
  }

  .gary:active {
    opacity: .6 !important;
    background-color: #f1f1f1;

  }

  .inputStep div {
    width: 1.2rem;
    font-size: 0.5rem;
    text-align: center;
  }

  .card_btn p:first-child {
    height: 0.8rem;
    line-height: 0.8rem;
  }

  .card_btn p:last-child {
    font-size: 0.3rem;
    height: 0.5rem;
    line-height: 0.15rem;
  }
</style>



