<template>
  <div class="f-model" v-show="isShow">
    <div class="f-model-bg" @click="handCancel">
      <transition :name="transitionname">
      <div class="f-model-card" v-show="isShow">
        <header v-show="iShead" class="f-model-card-head">
          {{title}}
        </header>
        <section class="f-model-card-body">
          <slot>{{text}}</slot>
        </section>
        <footer v-show="iSfoot" class="f-model-card-foot">
          <div v-if="isCancelBtn" @click="handCancel" class="foot-r">{{cancetext}}</div>
          <div v-if="isokBtn" @click="handOK" class="foot-l">{{oktext}}</div>
        </footer>
      </div>

      </transition>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        isActive:false,
      }
    },
    props: {
       title: {
        type: String,
        default: '温馨提示',
      },
      text: {
        type: String,
        default: '内容',
      },
      isShow:{
        type: Boolean,
        default: false,
      },
      iShead:{
        type: Boolean,
        default: true,
      },
      isCancelBtn:{
        type: Boolean,
        default: true,
      },
      isokBtn:{
        type: Boolean,
        default: true,
      },
      iSfoot:{
        type: Boolean,
        default: true,
      },
      oktext: {
        type: String,
        default: '确认',
      },
      transitionname: {
        type: String,
        default: 'fade',
      },
      cancetext: {
        type: String,
        default: '取消',
      },
      Section:{
        type:Array,
        default(){
          return [0,10]
        }
      },
      onOk:{
        type: Function,
        default(){

        }
      },
      onCancel:{
        type: Function,
        default(){
        }
      },
    },
    computed: {

    },
    methods: {
      handOK(){
        this.onOk();
        this.isActive = false;
        this.$emit('close');
      },
      handCancel(){
        this.onCancel();
        this.isActive = false;
        this.$emit('close');
      },
    },
    computed: {

    },
    mounted(){

    },
    watch: {
      isShow(val){
        this.isActive = val;
        console.log(val);
      }
    }

  }
</script>
<style scoped>
  .f-model{
    position:fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index:999;
  }
  .f-model-bg{
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.2);
  }

  .f-model-card{
    width: 90%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: #fff;
    border-radius: 0.1rem;
  }
  .f-model-card-head{
    width: 100%;
    height: 1.2rem;
    line-height: 1.2rem;
    padding-left: 0.5rem;
    box-sizing: border-box;
    font-size: 0.35rem;
  }

  .f-model-card-body{
    min-height: 1rem;
    padding: 0.5rem;
    font-size: 0.35rem;
    text-align: center
  ;
  }

  .f-model-card-foot{
    width: 100%;
    height: 1.2rem;
    line-height: 1.2rem;
    /*border: 1px solid #000;*/
    display: flex;
    justify-content: space-between;
    color: #E93E36;
    border-top: 1px solid #DADFE3;
  }
  .f-model-card-foot div{
    font-size: 0.35rem;
    flex:1;
    text-align: center;
  }
  .f-model-card-foot div:active{
    background-color: #f7f7f7;
  }
  .f-model-card-foot div:last-child{
    border-left: 1px solid #DADFE3;
  }

</style>



