function numLimeit(min, max) {
  var num;
  $('.item_l').click(function () {
    num = $(this).next().find('.item_num').text();
    $(this).next().next().removeClass('gary');
    if (num > min) {
      num--;
    }
    if (num == min) {
      $(this).addClass('gary');
    } else {
      $(this).removeClass('gary');
    }

    $(this).next().find('.item_num').text(num);
  });
  $('.item_r').click(function () {
    num = $(this).prev().find('.item_num').text();
    $(this).prev().prev().removeClass('gary');
    if (num < max) {
      num++;
    }
    if (num == max) {
      $(this).addClass('gary');
    } else {
      $(this).removeClass('gary');
    }
    console.log(num);
    $(this).prev().find('.item_num').text(num);
  });
};

function commonInit() {
  $('body').scrollTop(0);
}
function flexbox(dom1, dom2, fun1, fun2) {
  /*获取dom元素*/
  var left = document.querySelector(dom2);
  var ulbox = left.querySelector(dom1);
  console.log(ulbox);
  var leftHeight = left.offsetHeight;
  var ulHeight = ulbox.offsetHeight;
  console.log(leftHeight + "---------------------" + ulHeight);
  /*touch事件*/
  var starty = 0;
  var movey = 0;
  var distancey = 0;
  var currenty = 0;
  /*找到最大/小位置 弹簧最大/小位置*/
  var minposition = leftHeight - ulHeight;
  var maxposition = 0;
  var minspringposition = minposition - 150;
  var maxspringposition = maxposition + 150;
  ulbox.addEventListener("touchstart", function (e) {
    starty = e.touches[0].clientY;
  });
  ulbox.addEventListener("touchmove", function (e) {
    movey = e.touches[0].clientY;
    distancey = movey - starty;

    /*如果超出弹簧的返回就不再做动作了*/
    if (distancey + currenty < minspringposition || distancey + currenty > maxspringposition) {
      return;
    }
    //设置偏移
    closetransition();
    settransform(distancey + currenty);
  });
  ulbox.addEventListener("touchend", function (e) {
    /*判断 在弹簧范围之外已经在move中禁止移动了
     所以如果超过极限位置之外 回到极限位置 开启过度 设置当前位置(极限值)*/
    if (distancey + currenty < minposition) {
      currenty = minposition;
      opentransition();
      settransform(minposition);
      fun1 && fun1();
      console.log('到顶了');
    } else if (distancey + currenty > maxposition) {
      currenty = maxposition;
      opentransition();
      settransform(maxposition);
      console.log('到底了');
      fun2 && fun2();
    } else {
      currenty += distancey;
    }
  });

  /*设置偏移*/
  var settransform = function settransform(distance) {
    ulbox.style.transform = "translateY(" + distance + "px)";
    ulbox.style.webkitTransform = "translateY(" + distance + "px)";
  };
  /*开启过渡*/
  var opentransition = function opentransition() {
    ulbox.style.transition = "all 0.2s";
    ulbox.style.webkitTransition = "all 0.2s";
  };
  /*关闭过渡*/
  var closetransition = function closetransition() {
    ulbox.style.transition = "none";
    ulbox.style.webkitTransition = "none";
  };
}

