<template>
  <div>
    <div class="loader">
      <div class="loader-s">
        <div class="loader-inner line-scale-pulse-out-rapid">
				  <div></div>
				  <div></div>
				  <div></div>
				  <div></div>
				  <div></div>
			  </div>
      </div>
		</div>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  created() {},
  props: {
    value: {
      type: [String, Number],
      default: ""
    },
    title: {
      type: String,
      default: ""
    }
  },
  methods: {},
  beforeMount() {
    let parent;
    parent = document.querySelector(`.notifications.${this.placement}`);

    if (!parent) {
      parent = document.createElement("div");
      parent.classList.add("notifications", this.placement);
      document.body.appendChild(parent);
    }
    parent.innerHTML = "";
    parent.appendChild(this.$el);
  },
  computed: {},

  mounted() {},
  watch: {

  }
};
</script>
<style scoped>

</style>



