import Vue from 'vue';
import prompt from './prompt.vue';

function show(propsData) {
    var promptComponent = Vue.extend(prompt);
    return new promptComponent({
        el: document.createElement('div'),
        propsData,
    });
}

export default {
    open(params) {
        const defaultParam = { direction: 'right', duration: 4500 };
        const propsData = Object.assign(defaultParam, params);
        return show(propsData);
      },
};



