<template>
  <div class="calDisable">
    <slot></slot>
    <div v-if='showcal' class="datebg">
      <div class="dateHead">
        <div class="title_l">
          <i class="icon-ArtboardCopy" @click="close"></i>
        </div>
        <div class="title">
          <ul >
            <li>日</li>
            <li>一</li>
            <li>二</li>
            <li>三</li>
            <li>四</li>
            <li>五</li>
            <li>六</li>
          </ul>
        </div>
      </div>
      <div class="son">
        <ul class="time-body clearfix" v-for="(items,idx) in dateObj.date">
          <div class="dateTitle">{{items.mouth}}</div>
          <li v-for="n in items.week" class="nochonse"></li>
          <li v-for="(item,idxs) in items.datelist" ref="liDom"
              :class="markDate_Li(item)"
              @click="getTime(item.date,$event)">
            <span>{{item.date|timeFilter}}</span>
            <span v-if="item.date!=beginDate" :class="item.date==nowdate?'now-date':'dn'">今</span>
            <i>{{markDate(item.date)}}</i>
          </li>
        </ul>
        <div style="height: 2rem;"></div>
      </div>
    </div>
  </div>
</template>

<script>
/* 目前可以完善的功能有
1- 跨月
2- 显示的字段 (外)
3- 限制输入的时间范围 (外)
4- 返回的时间格式 (可选择)
*/
  function getDateObj(date) {
    var date1 = moment().subtract(1, 'days')
    var date2 = moment().add(3, 'months').add(1, 'days');
    if (date) {
      var nowDate = date
    } else {
      var nowDate = moment();
    }

    //循环 生成dateObj 最后吧dateObj push 到数组里面;
    var list = []
    for(var i=0;i<4;i++){
      var obj = {
        firstDay:'',
        days:'',
        week:'',
        mouth:'',
        datelist:[],
        statueStr:'入',
      }
      obj.firstDay = moment().add(i, 'months').startOf('month');//当月第一天 2017-11-24
      obj.days = moment().add(i, 'months').daysInMonth();//当月的天数
      obj.week = obj.firstDay.format('d') - 0;//第一天的星期数
      obj.mouth = obj.firstDay.format('YYYY-MM');//显示的月份格式
      obj.datelist = [];//每月的日期天数
      for (var j = 0; j < obj.days; j++) {
        var date = moment().add(i, 'months').startOf('month').add(j, 'days').format('YYYY-MM-DD');//当前月份第一天往后面生成日期
        var flag = moment(date).isBetween(date1, date2);
        obj.datelist.push({date: date,flag: flag})
      }
      list.push(obj);
    }
    return {
      today: moment().format('YYYY-MM-DD'),
      date: list
    }
  }
  import moment from 'moment'
  export default {
    data: function () {
      return {
        dateObj: '',
        idxF: '',
        idxS: '',
        beginDate: this.begin,
        endDate: this.end,
        dateFlag: false,
        isshow: this.showcal,
        isfix: false,
        clicknum: 0,
        nowdate: moment().format('YYYY-MM-DD'),
      }
    },
    props: {
      begin: {
        type: String,
        default: moment().format('YYYY-MM-DD'),
      },
      end: {
        type: String,
        default: moment().add(1, 'days').format('YYYY-MM-DD'),
      },
      showcal: {
        type: Boolean,
        default: false,
      },
    },
    filters: {
      timeFilter: function (item) {
        return moment(item).format('D')
      },

      weekFilter: function (item) {
        var week = moment(item).format('d')
        return week
        if (week == 6 || week == 0) {
          return 'red'
        }
      }
    },
    computed:{

    },
    created: function () {
      this.getDate();
    },
    mounted: function () {
      var _t = this;


    },
    methods: {
      markDate(date){
        var str = '';
        if(date == this.beginDate){
          str = '入'
        }
        if(date == this.endDate){
          str = '离'
        }
        if(date != this.endDate && date != this.beginDate){
          str = '';
        }
        return str;
      },
      markDate_Li(item){
        var str = '';
        if(item.date == this.nowdate){
          str += ' now'
        }
        if(item.date == this.beginDate){
          str += ' beginDate'
        }
        if(item.date == this.endDate){
          str += ' endDate'
        }
        if(moment(item.date).isBetween(this.beginDate,this.endDate)){
          str += ' bechecked'
        }
        if(!item.flag) str += ' gray-a1';
        return str;
      },

      getTime(date, e) {
        var _t = this;
        _t.clicknum++;
        if (_t.clicknum == 1) {
          _t.beginDate = '';
          _t.endDate = ''
        }
        var nowDate = date;
        var d = moment(nowDate).isSameOrAfter(moment(_t.nowdate));
        if (!d) return
        if (!this.beginDate) {
          this.beginDate = nowDate;
        } else {
          var after = moment(this.beginDate).add(31, 'days');
          var flag = moment(nowDate).isAfter(this.beginDate);//当前时间在不在开始日期之前
          var flag2 = moment(nowDate).isBetween(this.beginDate, after);//是否介于当前时间和31天之后的时间

          if(!flag){
            this.beginDate = nowDate;
            return
          }
          if (flag2) {
            this.endDate = nowDate;
            setTimeout(function () {
              _t.isshow = false;
              _t.$emit('close');
              _t.$emit('changeVal', {beginDate: _t.beginDate, endDate: _t.endDate});
            }, 400)
          }
        }
      },

      close(){
        this.$emit('close');
      },

      getDate(date) {
        this.dateObj = getDateObj(date);
      },
    },
    watch: {
      showcal(val){
        this.clicknum = 0;
      },

    }
  }
</script>

<style scoped>
body {
  margin: 0;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.gray-a1 {
  color: #a1a1a1 !important;
}

.time-body li:nth-child(7n + 1),
.time-body li:nth-child(7n + 2) {
  color: #e93e36;
}

.now,
.red {
  color: #e93e36;
}

.dn {
  display: none !important;
}

.fix {
  position: fixed;
  transition: all 0.5s;
  width: 100%;
  top: 0;
  left: 0;
  background-color: #fff;
  box-shadow: 0 1px 3px 0 #ccc;
}

.dateTitle {
  font-size: 0.54rem;
  text-align: center;
  color: #3f3f3f;
}

.datebg {
  position: fixed;
  padding-top: 1.8rem;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 100;
}

.son {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  top: 0;
  left: 0;
}

.dateHead {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1.5rem;
}

.dateHead i {
  height: 0.5rem;
  padding-top: 0.2rem;
  margin-top: 0.2rem;
  margin-left: 0.3rem;
}

.title_l {
  margin-top: 0.3rem;
}

.title {
  width: 100%;
  height: 1rem;
  box-shadow: 0 2px 2px 0 #ccc;
}
.title ul {
  width: calc(100% - 0.2rem);
  line-height: 1rem;
  text-align: center;
  font-size: 0.35rem;
  display: flex;
  justify-content: space-between;
}

.title li {
  flex: 1;
}

.beginDate {
  border-radius: 50% 0 0 50%;
  background-color: #e93e36 !important;
  color: #fff;
}

.endDate {
  border-radius: 0 50% 50% 0;
  background-color: #e93e36 !important;
  color: #fff;
}

.endDate span,
.endDate i,
.beginDate span,
.beginDate em {
  color: #fff;
}

.bechecked {
  background: rgba(233, 62, 54, 0.4) !important;
  color: #fff;
}

.time-body {
  margin-top: 0.3rem;
}

.time-body li {
  float: left;
  width: 14%;
  height: 1rem;
  line-height: 1rem;
  text-align: center;
  background-color: #fff;
  font-size: 0.3rem;
}

.time-body li p {
  height: 50%;
}

.nochonse {
  background-color: #fff !important;
}

</style>



